import React from 'react';

export default function Filtros() {
	return (
		<div className='filtros'>
			<div className='filtro'>
				<img src='/static/icons/icons8-filter-50.png' width='20px' alt='' /> Filtros
			</div>

			<div className='vista'>
				<img src='/static/icons/icons8-view-stream-50.png' width='20px' alt='' /> Filtros
			</div>
			<style jsx>{`
				.filtros {
					display: flex;
					justify-content: flex-end;
					align-items: center;
					max-width: 900px;
					padding: 2em;
				}
			`}</style>
		</div>
	);
}
